* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 5px;
  border: 2px solid lightskyblue;
  
}

#search-box:hover, #search-box:focus{
  border: 2px solid rgb(101, 191, 247);
  outline: none;
}

.bold{
  font-weight: bold;
}



.country-list{
  list-style: circle;
}

.country-list > .countryList-item{
  display: flex;
  align-items: center;
}

.countryList-item :not(:last-child){
  margin-right: 10px;
  font-size: 20px;
}

.onlyCountryName{
  font-size: 40px;
  font-weight: bolder;
}

.onlyCountryInfo{
  font-size: 25px;
}